@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
*{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #fff;
  position: -webkit-sticky;
  position: sticky;
  justify-content: space-between;
  top: 0;
  z-index: 100;
  padding: 3%;
}

.header__logo {
  width: 100px;
  object-fit: contain;
  margin: 0 20px;
  /* margin-top: 18px;  */
}

.header__shopping{
  border-radius: 50%;
  padding: 10px;
  background-color: #f3f3f3;
  cursor: pointer;
}

.header__search {
  display: flex;
  align-items: center;
  border-radius: 24px;
  margin: auto;
  width: 70%;
  background-color: #f3f3f3;
  height: 50px;
  padding: 2%;
}

.header__searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
  background-color: #f3f3f3;
  color: black;
  font-size: large;
}

.header__searchInput:focus {
height:15px;
width: 100%;
border-bottom: 1px solid lightgray;
border: none;
}


.slider__Iconsbox{
  display: flex;
  flex-direction: column;
  /* margin: 10px; */
}
.slider__Iconbar{
  display: flex;
  padding: 5px;
}

.slider__Iconbar > p{
  margin: 10px;
  color: gray;
}
.slider__Icon{
  font-size: large;
  margin: 10px;
}

.slider__divider{
  margin: 2px 0 0 0;
  width: 100%;
  border-bottom: 1px solid gray;
}

@media screen and (max-width:400px) {
  .header__search{
    height: 30px;
  }
}
.swiper{
  margin: 2% 0;
  padding: 3% 0;
  width: 100%;
}

.swiper__slide{
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper__image{
   margin-bottom: 40px;
   border-radius: 8%;
   width: 100%;
   height: 100%;
   display: block;
   object-fit: cover;
}
.classes{
    display: flex;
    flex-direction: column;
}
.classes__school, .classes__compitions{
    display: flex;
    margin: 10px;
}

.classes__container{
    justify-content: space-evenly;
    margin: auto;
    text-align: center;
}
.classes__box > p{
    text-align: center;
    font-weight: bolder;
    font-size: 40px;
    position:static;
    padding-top: 36%;
    font-family: 'Playfair Display', serif;
}
.classes__box {
    background-color: rgb(184, 207, 238);
    color:rgb(9, 63, 134);
    width: 200px;
    height: 200px;
    border-radius: 18%;
    margin-bottom: 10%;
}
@media screen and (max-width:1060px) {
    .classes__box{
        width: 150px;
        height: 150px;
    }
    .classes__box > p{
        font-size: 30px;
    }
}

@media screen and (max-width:786px) {
    .classes__box{
        width: 100px;
        height: 100px;
    }
    .classes__box > p{
        font-size: 20px;
    }
}

@media screen and (max-width:540px) {
    .classes__box{
        width: 75px;
        height: 75px;
    }
    .classes__box > p{
        font-size: 13px;
    }
}

@media screen and (max-width:400px) {
    .classes__box{
        width: 50px;
        height: 50px;
    }
    .classes__box > p{
        font-size: 10px;
    }
    .classes__container > span{
        font-size: 12px;
    }
}

.novel__slider{
    width: 600px;
    box-sizing: content-box;
    padding: 2%;
}

.novel__image{
    display: block;
    object-fit: cover;
    border-radius: 40px;
    width: 600px;
    height: 200px;
}

.novel__heading{
    font-size: 34px;
    margin: 2%;
}

@media screen and (max-width:786px) {
    .novel__slider{
        width: 300px;
    }
    .novel__image{
        width: 300px;
        height: 100px ;
        border-radius: 20px;
    }
    .novel__heading{
        font-size: 20px;
    }
}
*{
    font-family: 'Playfair Display', serif;
}
.book__slider{
    text-align: left;
    width: 300px;
    padding: 2%;
}
.book__box{
    width: 300px;
    height: 300px;
    background-color: #f3f3f3;
    border-radius: 20px;
    text-align: center;
    justify-content: center;
    box-sizing: border-box;
    padding: 10%;
}

.book__image{
    display: block;
    margin: auto;
    object-fit: contain;
    width: 200px;
    height: 250px;
}

.book__heading{
    font-size: 34px;
    margin: 2%;
}

.book__discription{
    display: flex;
    flex-direction: column;
}
/* .book__name{
    
} */
.book__price{
    color: rgb(60, 20, 238);
    font-weight: bold;
}
.book__actualPrice{
    color: gray;
    text-decoration: line-through;
    font-weight: bold;

}
.book__off{
    color: rgb(9, 240, 86);
    font-weight: bold;
}

@media screen and (max-width:786px) {
    .book__slider{
        width: 150px;
    }
    .book__box{
        width: 150px;
        height: 150px;
    }
    .book__image{
        width: 100px;
        height: 125px;
    }
}

@media screen and (max-width:786px) {

    .book__heading{
        font-size: 20px;
    }
    
}
